// --- main --- //

function Item(props){
    let {to, url, onClick, label, } = props;
    const nav = useNavigate();
    const css = (to || url) ? "lds-demos-link" : "lds-demos-link is-nolink";

    if(!onClick) onClick = () => {
        utils.nav(nav, to);
    }

    const onDemoClick = () => {
        // const {name, } = item;
        // dispatch({call: "SET_DEMO", value: item.name, });
        // console.log("==== yoooooo: ", item);
        if(url) window.location = url;
        else utils.nav(nav, to);
    }

    // if(url) return <div><a href={url}>{label}</a></div>
    return <div><div className={css} onClick={() => onDemoClick()} >{label}</div></div>;
}

function Section(props){
    const {title, description, children} = props;

    return (
        <div className="lds-demos-section">
            {title && <h2>{title}</h2>}
            {description && <p>{description}</p>}
            {children}
        </div>
    );
}

