// --- main --- //



export default function Page(props){
    // const {onboard1, } = useGlobals();
    const location = useLocation();
    const step = getStepParam(location);
    const props2 = {steps, };

    // on stap change
    React.useEffect(() => {
        window.scrollTo(0, 0);
    }, [step]);

    // step
    const stepProp = (step >= 3) ? (step-3) : undefined;
    console.log("========== STEP: " + step + ", PROP: " + stepProp);

    // pages
    const Pages = [
        SingleOwnerEmail,
        PasswordPage,
        LoginPage,
        WelcomePage,
        AccountPage,
        AgreePage,
        DesignPage,
        DesignPage2,
        HoursPage,
        DepartmentsPage,
        EmployeesPage,
        PatientsPage,
        CompletedPage,
    ]

    // staps
    const Page = Pages[step] || Pages[0];
    return <Page {...props2} index={step} back={String(step-1)} next={String(step+1)} step={stepProp} />
}

